
import * as Vue from "vue";
import Data from '@/lib/Data';
import IMilestones from '@/interfaces/IMilestones';

export default Vue.defineComponent({
  setup() {
    return {
      milestones: Vue.ref<IMilestones>({}),
    }
  },
  async mounted() {
    this.milestones = await Data.fetchMilestones();
  }
});
